import React, { useContext, useEffect, useState } from 'react';
import { Context } from '../..';
import { observer } from 'mobx-react-lite';
import { useHistory } from 'react-router-dom';

import Prompt from '../Prompt/Prompt';
import whiteArrow from '../../assets/images/white-arrow-right.svg';
import smallArrowDownSvg from '../../assets/images/smallArrowDown.svg';

import './miniForm.scss';



const MiniForm = observer(() => {
    const{russianCities} = useContext(Context);     //—Ä—É—Å—Å–∫–∏–µ –≥–æ—Ä–æ–¥–∞
    const {chineseCities} = useContext(Context);    //–∫–∏—Ç–∞–π—Å–∫–∏–µ –≥–æ—Ä–æ–¥–∞
    const{currency} = useContext(Context);          //–∫—É—Ä—Å –≤–∞–ª—é—Ç
    const{options} = useContext(Context);           //–≤—ã–±—Ä–∞–Ω–Ω—ã–µ –æ–ø—Ü–∏–∏

    const [promptActive, setPromptActive] = useState(false);    //—Å–æ—Å—Ç–æ—è–Ω–∏–µ –ø–æ–¥—Å–∫–∞–∑–∫–∏

    const [currentCurrency, setCurrentCurrency] = useState(currency.selectedCurrency.name); //—Å—Ç—ç–π—Ç –≤—ã–±–æ—Ä–∞ –≤–∞–ª—é—Ç—ã
    const [currentRate, setRate] = useState(currency.selectedCurrency.rate);     //—Å—Ç—ç–π—Ç –∫—É—Ä—Å–∞ –≤–∞–ª—é—Ç
    
    const [currentRussianCity, setRussianCity] = useState(russianCities.russianCities[0].name);     //—Å—Ç—ç–π—Ç –¥–ª—è —Ä—É—Å—Å–∫–æ–≥–æ –≥–æ—Ä–æ–¥–∞
    const [currentChineseCity, setChineseCity] = useState('');      //—Å—Ç—ç–π—Ç –∏–Ω–ø—É—Ç–∞ —Å –≤—ã–±–æ—Ä–æ–º –∫–∏—Ç–∞–π—Å–∫–æ–≥–æ –≥–æ—Ä–æ–¥–∞

    const history = useHistory();


    const selectRusCity = document.getElementById('form__item--to');
    const selectUsd = document.getElementById('form__item--usd');



    //–ü–æ—è–≤–ª–µ–Ω–∏–µ –ø–æ–¥—Å–∫–∞–∑–∫–∏:
    useEffect(() => {
        setTimeout(() => {
                setPromptActive(true);
                }, 1500)
    }, []);



    //---–ê–≤—Ç–æ–∫–æ–º–ø–ª–∏—Ç —Å –∫–∏—Ç–∞–π—Å–∫–∏–º–∏ –≥–æ—Ä–æ–¥–∞–º–∏---:
    function showChineseResults(value) {    //–ø–æ–ª—É—á–∞–µ–º –≤–≤–æ–¥ –∏–∑ –∏–Ω–ø—É—Ç–∞
        const selectChinaCity = document.getElementById('form__item--from');
        const selectChinaCity_title = selectChinaCity.querySelector('.form__item--select__title');
        
        setChineseCity(value);
        setPromptActive(false);

        function autocomplete(val) {    //–∏—â–µ—Ç –∞–π—Ç–µ–º—ã –ø–æ —Å–æ–≤–ø–∞–¥–µ–Ω–∏—é
            if (val === '') {
              return [];
            }
            const arrCities = [];   //—Å–æ–∑–¥–∞—ë–º –º–∞—Å—Å–∏–≤ –∏–∑ –Ω–∞–∑–≤–∞–Ω–∏–π –≥–æ—Ä–æ–¥–æ–≤
            chineseCities.chineseCities.forEach(item => {
                arrCities.push(item.name);
            })
    
            var reg = new RegExp(`^${val}`, 'i');       //—Ä–µ–≥—É–ª—è—Ä–∫–∞ —Å –≤–≤–æ–¥–∏–º—ã–º –∑–Ω–∞—á–µ–Ω–∏–µ–º –∏–∑ –∏–Ω–ø—É—Ç–∞
            let result = arrCities.filter(item => item.match(reg));
            return result;
        }

        let elem = document.getElementById('results');  //–≤—ã—Ç–∞—Å–∫–∏–≤–∞–µ–º –±–ª–æ–∫ —Å —Ä–µ–∑—É–ª—å—Ç–∞—Ç–∞–º–∏
        elem.innerHTML = '';
        let list = '';

        let shownItems = autocomplete(value);           //–Ω–∞—Ö–æ–¥–∏–º –∞–π—Ç–µ–º—ã –ø–æ —Å–æ–≤–ø–∞–¥–µ–Ω–∏—é —Å –≤–≤–æ–¥–æ–º –∏–∑ –∏–Ω–ø—É—Ç–∞
        
        //–†–∏—Å—É–µ–º —ç–ª-—Ç—ã:
        for (let i = 0; i < shownItems.length; i++) {
            const option = `<div class="select-item">
                <input type='radio' class='select__input' id='city-name'></input>
                <label for='city-name' className='select__label'>${shownItems[i]}</label>
            </div>`
            list += option;
        }
        elem.innerHTML = `${list}`;
        
        //–í—ã–ø–∞–¥–∞—é—â–µ–µ –º–µ–Ω—é:
        selectChinaCity_title.oninput = function() {
            selectChinaCity.setAttribute('data-state', 'active');
        }

        //–ó–∞–∫—Ä—ã–≤–∞–µ–º –ø–æ –∫–ª–∏–∫—É –Ω–∞ –æ–ø—Ü–∏—é –∏ —Å–æ—Ö—Ä–∞–Ω—è–µ–º –≤ —Å—Ç–µ–π—Ç:
        const selectChinaCity_items = selectChinaCity.querySelectorAll('.select-item');
        for (let i = 0; i < selectChinaCity_items.length; i++) {
            selectChinaCity_items[i].addEventListener('click', (e) => {
                selectChinaCity_title.textContent = e.currentTarget.textContent;
                selectChinaCity.setAttribute('data-state', '');

                setChineseCity(e.target.textContent.trim());    //–∑–∞–ø–∏—Å—ã–≤–∞–µ–º –≤ —Å—Ç–µ–π—Ç
            })
        }
    }
    


    //---–í—ã–±–æ—Ä –∫—É–¥–∞---:
    function onChangeRussianCity() {
        setPromptActive(false);     //–¥–µ–∞–∫—Ç–∏–≤–∏—Ä—É–µ–º –ø–æ–¥—Å–∫–∞–∑–∫—É
        const selectRusCity_title = selectRusCity.querySelector('.form__item--select__title');

        //–í—ã–ø–∞–¥–∞—é—â–µ–µ –º–µ–Ω—é:
        selectRusCity_title.addEventListener('click', () => {
            selectRusCity.setAttribute('data-state', 'active');
        })
    }
    //–ó–∞–∫—Ä—ã–≤–∞–µ–º –ø–æ –∫–ª–∏–∫—É –Ω–∞ –æ–ø—Ü–∏—é –∏ –°–û–•–†–ê–ù–ï–ù–ò–ï –í –°–¢–ï–ô–¢:
    function onOptionRussian(item) {   //–ø–æ–ª—É—á–∞–µ–º –≤—ã–±—Ä–∞–Ω–Ω—ã–π –∞–π—Ç–µ–º –∏–∑ —Å–ø–∏—Å–∫–∞ —Ä—É—Å—Å–∫–∏—Ö –≥–æ—Ä–æ–¥–æ–≤
        selectRusCity.setAttribute('data-state', '');
        setRussianCity(item.name);
    }



    //---–í—ã–±–æ—Ä –≤–∞–ª—é—Ç—ã –∏ –µ—ë –∫—É—Ä—Å–∞---:
    const onChangeCurrency = (e) => {
        setPromptActive(false);     //–¥–µ–∞–∫—Ç–∏–≤–∏—Ä—É–µ–º –ø–æ–¥—Å–∫–∞–∑–∫—É
        const selectUsd_title = selectUsd.querySelector('.form__item--select__title');

        //–í—ã–ø–∞–¥–∞—é—â–µ–µ –º–µ–Ω—é:
        selectUsd_title.addEventListener('click', () => {
                selectUsd.setAttribute('data-state', 'active');
        })

    }
    //–ó–∞–∫—Ä—ã–≤–∞–µ–º –ø–æ –∫–ª–∏–∫—É –Ω–∞ –æ–ø—Ü–∏—é –∏ –°–û–•–†–ê–ù–ï–ù–ò–ï –í –°–¢–ï–ô–¢:
    function onOptionCurrency(item) {   //–ø–æ–ª—É—á–∞–µ–º –≤—ã–±—Ä–∞–Ω–Ω—ã–π –∞–π—Ç–µ–º –∏–∑ —Å–ø–∏—Å–∫–∞ –≤–∞–ª—é—Ç
        selectUsd.setAttribute('data-state', '');
        setCurrentCurrency(item.name);

        //–ú–µ–Ω—è–µ–º –∫—É—Ä—Å –≤–∞–ª—é—Ç—ã:
        setRate(item.rate);
    }



    //---–°–æ–±–∏—Ä–∞–µ–º –≤—ã–±—Ä–∞–Ω–Ω—ã–µ –¥–∞–Ω–Ω—ã–µ---:
    const onToSelect = (e) => {
        const selectedOptions = {   //–æ–±-–∫—Ç —Å –≤—ã–±—Ä–∞–Ω–Ω—ã–º–∏ –¥–∞–Ω–Ω—ã–º–∏
            from: currentChineseCity,
            to: currentRussianCity,
            currency: currentCurrency
        }
        options.setOptions(selectedOptions);    //–∑–∞–ø–∏—Å—ã–≤–∞–µ–º –≤ miniForm —Å—Ç–æ—Ä
        history.push('/select');
    }


    return (
        <div>
            <div className='form-container'>
                <form className='form'>

                    {/**–û—Ç–∫—É–¥–∞: */}
                    <div className='form__item' id='form__item--from' data-state=''>
                        <input 
                            value={currentChineseCity} 
                            onChange={e => showChineseResults(e.target.value)}  
                            type='text'
                            className='form__item--select__title'
                        ></input>
                        <div className='form__item--select__content' id='results'></div>
                    </div>


                    {/**–ö—É–¥–∞: */}
                    <div onClick={() => onChangeRussianCity()} className='form__item' id='form__item--to' data-state=''>
                        <div className='form__item--select__title'>  {/**–≥–ª–∞–≤–Ω—ã–π –±–ª–æ–∫ —Å –Ω–∞–∑–≤–∞–Ω–∏–µ–º*/}
                            {currentRussianCity}
                        </div>

                        <div className='form__item--select__content'>
                            {russianCities.russianCities.map(item => {
                                return (
                                    <div className='select-item' key={item.id}>
                                        <input id="select0" className='select__input' type="radio"/>
                                        <label onClick={() => onOptionRussian(item)} htmlFor="select0" className='select__label'>{item.name}</label>
                                    </div>
                                )
                            })}
                       </div>
                    </div>


                    {/**–í–∞–ª—é—Ç–∞: */}
                    <div onClick={e => onChangeCurrency(e)} className='form__item' id='form__item--usd' data-state=''>
                        <div className='form__item--select__title'>  {/**–≥–ª–∞–≤–Ω—ã–π –±–ª–æ–∫ —Å –Ω–∞–∑–≤–∞–Ω–∏–µ–º*/}
                            {currentCurrency}
                        </div>

                       <div className='form__item--select__content'>
                            {currency.currency.map(item => {
                                return (
                                    <div className='select-item' key={item.id}>
                                        <input id="select0" className='select__input' type="radio"/>
                                        <label onClick={() => onOptionCurrency(item)} htmlFor="select0" className='select__label'>{item.name}</label>
                                    </div>
                                )
                            })}
                       </div>
                    </div>


                    {/**–ö—É—Ä—Å: */}
                    <div className='form__item form__item--rate'>
                        <p>{currentRate}</p>
                    </div>
                </form>


                <button onClick={onToSelect} type='submit' className='form__nextBtn'> –î–∞–ª–µ–µ <img src={whiteArrow} alt='/'/></button>
            </div>


            <Prompt active={promptActive} setActive={setPromptActive}/>
        </div>
    )
})

export default MiniForm;