import React, { useContext, useState } from 'react';
import { observer } from 'mobx-react-lite';

import './selectionInfo.scss';
import './../../styles/index.scss';

import selectedItemImg from './../../assets/images/coach.png';
import add from './../../assets/images/blue-add.svg';
import reduce from './../../assets/images/blue-reduce.svg';
import { Link } from 'react-router-dom/cjs/react-router-dom.min';
import { Context } from '../..';



const SelectionInfo = observer(({selectedItem}) => {    //–ø–µ—Ä–µ–¥–∞–ª–∏ –ø—Ä–æ–ø—Å–æ–º –≤—ã–±—Ä–∞–Ω–Ω—ã–π –∞–π—Ç–µ–º –∏–∑ SelectionPage.jsx
    const {furniture} = useContext(Context);

    const [isSelected, setSelected] = useState(selectedItem);   //—Å—Ç—ç–π—Ç –æ—Ç–æ–±—Ä–∞–∂–µ–Ω–∏—è –≤—ã–±—Ä–∞–Ω–Ω–æ–≥–æ –∞–π—Ç–µ–º–∞ –º–µ–±–µ–ª–∏
    const [total, setTotal] = useState(1);

    //–£–≤–µ–ª–∏—á–µ–Ω–∏–µ –∏ —É–º–µ–Ω—å—à–µ–Ω–∏–µ –∫–æ–ª–∏—á–µ—Å—Ç–≤–∞ —à—Ç—É–∫:
    const increment = () => {
        setTotal(total + 1);
    }
    const decrement = () => {
        if (total === 1) {
            return;
        }
        setTotal(total - 1);
    }

    return (
        <div className='wrap'>
        {isSelected === selectedItem
            ? 
            <div className='info__empty'>
                {furniture.selectedFurniture.length > 0 ? <p>–í—ã–±–µ—Ä–µ—Ç–µ –µ—â–µ —ç–ª–µ–º–µ–Ω—Ç</p> : <p>–í—ã –Ω–µ –≤—ã–±—Ä–∞–ª–∏ –ø–æ–∫–∞ –Ω–∏ –æ–¥–Ω–æ–≥–æ —ç–ª–µ–º–µ–Ω—Ç–∞.</p>}
            </div>
            :
            <div className='info' id='info'>
                <div className='info__header'>
                    <p>–ó–∞—Ç–µ–º –∑–∞–ø–æ–ª–Ω–∏—Ç–µ —Å–ª–µ–¥—É—é—â–∏–µ<br></br>–ø–æ–ª—è –≤—ã–±—Ä–∞–Ω–Ω–æ–≥–æ —ç–ª–µ–º–µ–Ω—Ç–∞:</p>
                </div>
                <div className='info__selected-item'>
                    <img src={selectedItemImg} alt='/' />
                    <p>{selectedItem.name}</p>
                </div>


                <div className='info__quantity'>
                    <p className='info__quantity--quantity'>–ö–æ–ª-–≤–æ:</p>
                    <div className='info__quantity--btns'>
                        <button onClick={decrement} type='button'><img src={reduce} alt='-' /></button>
                        <p className='info__quantity--amount'>{total}</p>
                        <button onClick={increment} type='button'><img src={add} alt='+' /></button>
                    </div>
                </div>

                <div className='info__specifications'>
                    <input className='info__specifications-item' placeholder={`–û–±—â–∏–π –æ–±—ä–µ–º: ${selectedItem.m3} –º3`} disabled></input>
                    <input className='info__specifications-item' placeholder={`–û–±—â–∞—è –º–∞—Å—Å–∞ –Ω–µ—Ç—Ç–æ: ${selectedItem.netweight} –∫–≥`} disabled></input>
                    <input className='info__specifications-item' placeholder={`–û–±—â–∞—è –º–∞—Å—Å–∞ –±—Ä—É—Ç—Ç–æ: ${selectedItem.grossweight} –∫–≥`} disabled></input>
                    <input className='info__specifications-item' placeholder={`–°—Ç–æ–∏–º–æ—Å—Ç—å –æ–¥–Ω–æ–π –µ–¥–∏–Ω–∏—Ü—ã: ${selectedItem.price}`} disabled></input>
                </div>

                <form>
                    <button className='btn btn__blue btn__submit'>–°–±—Ä–æ—Å–∏—Ç—å</button>
                    <Link to="/selected">
                        <button onClick={e => e.preventDefault} className='btn btn__blue btn__submit'>–î–æ–±–∞–≤–∏—Ç—å</button>
                    </Link>
                </form>
            </div>
        }
        </div>
    )
})

export default SelectionInfo;